import React from 'react'
import './Header.css'
import logo from '../../assets/logo.png'
import Bars from '../../assets/bars.png'
import { useState } from 'react';
import { Link } from 'react-scroll';

const mobile = window.innerWidth<=768?true:false;


const Header = () => {
  const [menuOpened,setMenuOpened] = useState(false);

  const menuHandle = (e)=>{
    e.preventDefault();
      setMenuOpened(true);
  }

  return (
    <div className='header' id='Header'>
        <img src={logo} alt="" className='logo'/>
        {(menuOpened===false && mobile===true)?
        (<div
        style={{
          backgroundColor:'var(--appColor)',
          padding:'0.5rem',
          borderRadius:"5px"
        }} onClick={menuHandle}><img src={Bars} style={{
          display:'flex',
          alignSelf:'center',
          width:'2rem',
          height:'1.2rem',
        }} alt="" /></div>):(
        <ul className='header-menu'>
          {mobile?(<li onClick={()=>setMenuOpened(false)}>X</li>):""}
        <li>
          <Link onClick={()=>setMenuOpened(false)} to="root"
          span={true}
          smooth={true}>Home</Link>
          </li>
        <li>
        <Link onClick={()=>setMenuOpened(false)} to="programs"
          span={true}
          smooth={true}>Programs</Link>
          </li>
        <li >
        <Link onClick={()=>setMenuOpened(false)} to="reasons"
          span={true}
          smooth={true}>Why Us</Link>
          </li>
        <li ><Link onClick={()=>setMenuOpened(false)} to="testimonials"
        span={true}
        smooth={true}
        >
          Testimonials</Link></li>
    </ul>)}
    </div>
  )
}

export default Header
