import React from 'react'
import './Hero.css'
import '../Header/Header'
import Header from '../Header/Header'
import hero_image from '../../assets/hero_image.png'
import hero_image_back from '../../assets/hero_image_back.png'
import Heart from '../../assets/heart.png'
import Calories from '../../assets/calories.png'

import { motion } from 'framer-motion';
const transition = {type:'spring',duration:3}
const mobile = window.innerWidth<=768?true:false;

const Hero = () => {
  return (
    <div className="hero">
      <div className="blur blur-h"></div>
      <div className="left-h">
        <Header/>

        <div className="the-best-ad">
          <motion.div initial={{left:mobile?'150px':'205px'}} whileInView={{left:'8px'}}
          transition={{...transition,type:'tween'}}></motion.div>
          <span>the best fitness club in toun</span>
        </div>
        <div className='hero-text'>
          <div>
            <span className='stroke-text'>Shape </span>
            <span>Your</span>
          </div>
          <div>
            <span>Ideal Body</span>
          </div>
          <div>
            <span>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Doloribus nisi velit hic dolore aperiam debitis maiores quas pariatur corrupti id.</span>
          </div>

        </div>

        <div className='figures'>
          <div>
            <span>+140</span>
            <span>expert coachs</span>
          </div>
          <div>
            <span>+978</span>
            <span>members joined</span>
          </div>
          <div>
            <span>+140</span>
            <span>fitness programs</span>
          </div>
        </div>

        <div className='hero-buttons'>
          <button className='btn'>Get Started</button>
          <button className='btn'>Lern More</button>
        </div>

      </div>
      <div className="right-h">
        <button className='btn'>Join Now</button>
        <motion.div transition={transition}
        initial={{right:'-1rem'}}
        whileInView={{right:'4rem'}} className="heart-rate">
          <img src={Heart} alt="" />
          <span>Heart Rate</span>
          <span>116 bpm</span>
        </motion.div>

        <img src={hero_image} alt="" className='hero-image' />
        <motion.img
        transition={transition}
        initial={{right:'11rem'}}
        whileInView={{right:'20rem'}}  src={hero_image_back} alt="" className='hero-image-back'/>

        <motion.div
        transition={transition}
        initial={{right:'37rem'}}
        whileInView={{right:'28rem'}}  className="calories">
          <img src={Calories} alt="" />
          <div>
              <span>Calories Burned</span>
               <span>220 kcal</span>
          </div>
        </motion.div>
      </div>
    </div>
  )
}

export default Hero
